<script>
import global from "./utils/global";
import Vue from 'vue'

// Vue.use(require('vue-moment')); 
var token;

export default {
  created() {
  },
  onLaunch() {
    wx.login({
      success: res => {
        wx.request({
          url: "http://localhost:3000/auth/token",
          data: {
            code: res.code,
          },
          method: "GET",
          success: res => {
            token = res.data.data.accessToken;
            global.commit("getToken", token);
          }
        });
      },
      fail: () => {},
      complete: () => {}
    });
  }
};
</script>

<style>
.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0 0;
  box-sizing: border-box;
}
/* this rule will be remove */
* {
  transition: width 2s;
  -moz-transition: width 2s;
  -webkit-transition: width 2s;
  -o-transition: width 2s;
}
.weui-loadmore {
  width: 65%;
  margin: 1.5em auto;
  line-height: 1.6em;
  font-size: 14px;
  text-align: center;
}

.weui-loadmore__tips {
  display: inline-block;
  vertical-align: middle;
}

.weui-loadmore_line {
  border-top: 1px solid #e5e5e5;
  margin-top: 2em;
}

.weui-loadmore__tips_in-line {
  position: relative;
  top: -.9em;
  padding: 0 0.55em;
  background-color: #fff;
  color: #999;
}

.weui-loadmore__tips_in-dot {
  position: relative;
  padding: 0 0.16em;
  width: 4px;
  height: 1.6em;
}

.weui-loadmore__tips_in-dot:before {
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1px;
  margin-left: -2px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #e5e5e5;
}
.loading {
  display: inline-block;
  margin-right: 12px;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  background: transparent;
  border-radius: 50%;
  border: 2px solid #2d8cf0;
  border-color: #2d8cf0 #2d8cf0 #2d8cf0 transparent;
  animation: loading-spin 0.6s linear;
  animation-iteration-count: infinite;
}
@keyframes loading-spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.outsideDiv{
  width:100%;
}
.weui-loading {
  margin: 0 5px;
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  -webkit-animation: weuiLoading 1s steps(12, end) infinite;
  animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent url(data:image/svg+xml;base64,) no-repeat;
  background-size: 100%;
}
.weui-loadnomore {
  width: 65%;
  margin: 0.5em auto;
  line-height: 1.6em;
  font-size: 14px;
  text-align: center;
}
opacity05{
  opacity:0.5
}
</style>



